import { useEffect, useState } from 'react';
import { TimeInput, parseInputTime } from './TimeInput';
import TimeButtonGroup from './TimeButtonGroup';
import TimeView from '../components/TimeView';

let intervalId;
let intervalMs = 3;

const TimeDown = () => {
  const [time, setTime] = useState();
  const [running, isRunning] = useState(false);
  const [input, setInput] = useState('');

  useEffect(() => {
    setTime(parseInputTime(input));
  }, [input]);

  const onChange = (e) => {
    setInput(e.target.value);
  };

  const clickStart = () => {
    if (!time) return;
    let downDateTime = new Date(time.getTime());
    let endDateTime = new Date(time.getTime());
    endDateTime.setHours(0, 0, 0, 0);
    intervalId = setInterval(() => {
      downDateTime = new Date(downDateTime.getTime() - intervalMs);
      if (downDateTime <= endDateTime) {
        setTime(endDateTime);
        clearInterval(intervalId);
        isRunning(false);
      } else {
        setTime(downDateTime);
      }
    }, intervalMs);
    isRunning(true);
  };

  const clickStop = () => {
    clearInterval(intervalId);
    isRunning(false);
  };

  const clickClear = () => {
    clearInterval(intervalId);
    isRunning(false);
    setTime();
    setInput('');
  };

  return (
    <>
      <div className="text-center">
        <p>TimeDown</p>
        <TimeView time={time} running={running}></TimeView>
        <TimeInput onChange={onChange} value={input}></TimeInput>
        <TimeButtonGroup
          running={running}
          onClickStart={clickStart}
          onClickStop={clickStop}
          onClickClear={clickClear}
        ></TimeButtonGroup>
      </div>
    </>
  );
};
export default TimeDown;
