import { useEffect, useState } from 'react';
import TimeView from '../components/TimeView';
import TimeButtonGroup from './TimeButtonGroup';
import { TimeInput, parseInputTime } from './TimeInput';

// ID (setInterval clearInterval )
let intervalId;
// ()
let intervalMs = 9;

const TimeClock = () => {
  // ()
  const [time, setTime] = useState();
  // ()
  const [running, isRunning] = useState(false);
  // ()
  const [input, setInput] = useState('');

  // input 
  useEffect(() => {
    //  input 
    setTime(parseInputTime(input));
  }, [input]);

  // 
  const changeInput = (e) => {
    // 
    setInput(e.target.value);
  };

  // 
  const clickStart = () => {
    // 
    if (!time) return;
    // ()
    let dateTime = new Date(time.getTime());
    // ()()
    intervalId = setInterval(() => {
      //  time 
      //  React 
      dateTime = new Date(dateTime.getTime() + intervalMs);
      setTime(dateTime);
    }, intervalMs);
    //  true 
    isRunning(true);
  };

  // 
  const clickStop = () => {
    // 
    clearInterval(intervalId);
    //  false 
    isRunning(false);
  };

  // 
  const clickClear = () => {
    // 
    clearInterval(intervalId);
    //  false 
    isRunning(false);
    // 
    setTime();
    // 
    setInput('');
  };

  return (
    <>
      <div className="text-center">
        <p>TimeClock</p>
        {/* TimeView time: runnning: */}
        <TimeView time={time} running={running}></TimeView>
        {/* TimeInput onChange: value: */}
        <TimeInput onChange={changeInput} value={input}></TimeInput>
        {/* TimeButtonGroup */}
        {/*   runnning    : */}
        {/*   onClickStart: */}
        {/*   onClickStop : */}
        {/*   onClickClear: */}
        <TimeButtonGroup
          running={running}
          onClickStart={clickStart}
          onClickStop={clickStop}
          onClickClear={clickClear}
        ></TimeButtonGroup>
      </div>
    </>
  );
};
export default TimeClock;
