import { useEffect, useState } from 'react';
import TimeView from '../components/TimeView';
import TimeButtonGroup from './TimeButtonGroup';

// ID (setInterval clearInterval )
let intervalId;
// ()
let intervalMs = 9;

const TimeDate = () => {
  // ()
  const [time, setTime] = useState();
  // ()
  const [running, isRunning] = useState(false);

  // useEffect ()
  useEffect(() => {
    // ()
    intervalId = setInterval(() => setTime(new Date()), intervalMs);
    //  true 
    isRunning(true);
    //   ()
    return () => {
      // 
      clearInterval(intervalId);
    };
  }, []);

  // 
  const clickStart = () => {
    // ()
    intervalId = setInterval(() => setTime(new Date()), intervalMs);
    //  true 
    isRunning(true);
  };

  // 
  const clickStop = () => {
    // 
    clearInterval(intervalId);
    //  false 
    isRunning(false);
  };

  // 
  const clickClear = () => {
    // 
    clearInterval(intervalId);
    //  false 
    isRunning(false);
    // 
    setTime();
  };

  return (
    <>
      <div className="text-center">
        <p>TimeDate</p>
        {/* TimeView time: runnning: */}
        <TimeView time={time} running={running}></TimeView>
        {/* TimeButtonGroup */}
        {/*   runnning    : */}
        {/*   onClickStart: */}
        {/*   onClickStop : */}
        {/*   onClickClear: */}
        <TimeButtonGroup
          running={running}
          onClickStart={clickStart}
          onClickStop={clickStop}
          onClickClear={clickClear}
        ></TimeButtonGroup>
      </div>
    </>
  );
};
export default TimeDate;
