import { Typography } from '@material-tailwind/react';

// true:, false:)
const colors = {
  true: 'blue',
  false: 'blue-gray',
};

/**
 * Date  hh:mm:ss.SSS 
 * @param {Date} time - 
 * @returns {string} - hh:mm:ss.SSS 
 */
const formatTime = (time) => {
  if (time instanceof Date) {
    return formatTimeNums(
      time.getHours(),
      time.getMinutes(),
      time.getSeconds(),
      time.getMilliseconds()
    );
  } else {
    return formatTimeNums(0, 0, 0, 0);
  }
};

/**
 * Date  hh:mm:ss.SSS 
 * @param {number} hh - 
 * @param {number} mm - 
 * @returns {string} - hh:mm:ss.SSS 
 */

/**
 * hh:mm:ss.SSS 
 * @param {number} hh 
 * @param {number} mm 
 * @param {number} ss 
 * @param {number} ms 
 * @returns {string} - hh:mm:ss.SSS 
 */
const formatTimeNums = (hh, mm, ss, ms) => {
  let result = [hh, mm, ss].map((v, i) => String(v).padStart(2, '0')).join(':');
  result += '.' + String(ms).padStart(3, '0');
  return result;
};

/**
 * 
 * @component
 * @param {number} props.time - 
 * @param {boolean} props.running - 
 * @returns {JSX.Element} 
 */
const TimeView = ({ time, running }) => {
  return (
    <>
      <Typography variant="h4" color={colors[running]}>
        {formatTime(time)}
      </Typography>
    </>
  );
};
export default TimeView;
