import { useState } from 'react';
import { TimeInput, parseInputTime } from './TimeInput';
import TimeButtonGroup from './TimeButtonGroup';
import TimeView from '../components/TimeView';

let intervalId;
let intervalMs = 3;

const TimeUp = () => {
  const [time, setTime] = useState();
  const [running, isRunning] = useState(false);
  const [input, setInput] = useState('');

  const onChange = (e) => {
    setInput(e.target.value);
  };

  const clickStart = () => {
    if (!input) return;
    let endDateTime = parseInputTime(input);
    let upDateTime = new Date(endDateTime.getTime());
    upDateTime.setHours(0, 0, 0, 0);
    if (time) {
      upDateTime = new Date(time.getTime());
    }
    intervalId = setInterval(() => {
      upDateTime = new Date(upDateTime.getTime() + intervalMs);
      if (upDateTime >= endDateTime) {
        setTime(endDateTime);
        clearInterval(intervalId);
        isRunning(false);
      } else {
        setTime(upDateTime);
      }
    }, intervalMs);
    isRunning(true);
  };

  const clickStop = () => {
    clearInterval(intervalId);
    isRunning(false);
  };

  const clickClear = () => {
    clearInterval(intervalId);
    isRunning(false);
    setTime();
    setInput('');
  };

  return (
    <>
      <div className="text-center">
        <p>TimeUp</p>
        <TimeView time={time} running={running}></TimeView>
        <TimeInput onChange={onChange} value={input}></TimeInput>
        <TimeButtonGroup
          running={running}
          onClickStart={clickStart}
          onClickStop={clickStop}
          onClickClear={clickClear}
        ></TimeButtonGroup>
      </div>
    </>
  );
};
export default TimeUp;
