import { Button, ButtonGroup } from '@material-tailwind/react';

/**
 * 
 * @component
 * @param {boolean} props.running - 
 * @param {function} props.onClickStart - 
 * @param {function} props.onClickStop - 
 * @param {function} props.onClickClear - 
 * @returns {JSX.Element} 
 */ const TimeButtonGroup = ({
  running,
  onClickStart,
  onClickStop,
  onClickClear,
}) => {
  return (
    <>
      <div className="mb-5 flex justify-center">
        <ButtonGroup>
          <Button disabled={running} onClick={onClickStart}>
            start
          </Button>
          <Button disabled={!running} onClick={onClickStop}>
            stop
          </Button>
          <Button onClick={onClickClear}>clear</Button>
        </ButtonGroup>
      </div>
    </>
  );
};
export default TimeButtonGroup;
